
@font-face {
  font-family: ProximaLight;
  src: url(/static/fonts/ProximaNova-Light.106f81c.ttf);
}
@font-face {
  font-family: ProximaReg;
  src: url(/static/fonts/ProximaNova-Regular.d670e5f.ttf);
}
body {
  margin: 0;
  padding: 0;
}
.f-container {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.no-gutters {
  margin-right: 0;
  margin-left: 0;
}
.no-gutters > [class*="col-"],
  .no-gutters > .col {
    padding-right: 0;
    padding-left: 0;
}
.four-gutters {
  margin-right: 0;
  margin-left: 0;
}
.four-gutters > [class*="col-"],
  .four-gutters > .col {
    padding-right: 4px;
    padding-left: 4px;
}
.container {
  min-width: 970px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.l-aside {
  flex: 0 0 240px;
  max-width: 26%;
}
.r-aside {
  flex: 0 0 240px;
  max-width: 26%;
}
.f-container {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

nav {
  height: 66px;
  display: block;
  width: 100%;
  background-color: #87b448;
  border-bottom: 2px solid #71973c;
}
nav .nav__content {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    display: box;
    flex-wrap: nowrap;
    width: 100%;
}
nav .nav__item {
    width: 100%;
    height: 66px;
}
nav .nav__logo {
    height: 100%;
    align-self: center;
    width: auto;
}
nav .nav__logo img {
      margin-bottom: 6px;
}
nav .nav__search {
    padding-left: 30px;
    align-self: center;
    height: 34px;
}
nav .nav__search > input {
      height: 34px;
      border: none;
      width: 100%;
      background-color: #73993d;
      border-radius: 2px;
      line-height: 34px;
      vertical-align: middle;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: 16px;
      background-position: 10px;
      padding-left: 35px;
      color: white;
}
nav .nav__search > input::placeholder {
        color: white;
        opacity: 1;
        font-family: ProximaReg;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
}
nav .nav__notify {
    height: 100%;
    align-self: center;
    width: auto;
    padding-right: 21px;
    padding-left: 21px;
}
nav .nav__notify ul {
      text-align: right;
      white-space: nowrap;
      display: block;
      text-align: center;
      padding: 0;
      margin: 0;
      list-style: none;
      padding-left: 10px;
}
nav .nav__notify ul li {
        display: inline-block;
        padding-left: 10px;
        padding-right: 10px;
        cursor: pointer;
}
nav .nav__notify ul li img[src*='chat'] {
          width: 26px;
}
nav .nav__notify ul li img[src*='notifications'] {
          width: 24px;
}
nav .nav__profile {
    width: auto;
}
nav .nav__profile .profile__wrapp {
      white-space: nowrap;
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      display: box;
      flex-wrap: nowrap;
      flex-direction: row;
      height: 100%;
      align-items: center;
      justify-content: right;
}
nav .nav__profile .profile__wrapp .profile__photo img {
        width: 32px;
}
nav .nav__profile .profile__wrapp .profile__name a {
        color: white;
        width: 100%;
        overflow: hidden;
}
nav .nav__profile .profile__wrapp p {
        width: auto;
        padding-left: 11px;
        line-height: 32px;
        font-size: 16px;
        font-family: ProximaReg;
        margin: 0;
        color: white;
}

.aside hr {
  margin: 13px 0;
}
.aside > span {
  color: #b7bfcd;
  font-family: ProximaReg;
  font-size: 12px;
  line-height: 23px;
}
.aside > a {
  width: 50%;
  color: #b7bfcd;
  font-family: ProximaReg;
  font-size: 12px;
  padding-right: 21px;
}
.aside__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  margin-bottom: 18px;
}
.aside__menu li {
    border-bottom: 1px solid #e0e2e6;
    padding-top: 12px;
    padding-bottom: 12px;
}
.aside__menu li a {
      color: #59606b;
      font-size: 16px;
      line-height: 26px;
      font-family: ProximaReg;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      display: block;
}
.aside__menu li img {
      margin-right: 25px;
      display: block;
      width: 24px;
      height: 24px;
      float: left;
      object-fit: contain;
      object-position: center;
}
.adv__left h4 {
  font-family: ProximaReg;
  color: #7c8695;
  font-size: 14px;
  margin-bottom: 16px;
}
.adv__left img {
  display: block;
  width: 100%;
}
.adv__left p {
  font-size: 11px;
  font-family: ProximaReg;
  color: #7c8695;
  margin: 5px 0;
}
.featured-companies > h4 {
  display: inline-block;
  width: 65%;
  font-family: ProximaReg;
  font-size: 14px;
  color: #7c8695;
  margin: 0;
  padding: 0;
}
.featured-companies > span {
  display: inline-block;
  width: 34%;
  text-align: right;
  color: #59606b;
  font-size: 14px;
  font-family: ProximaReg;
}
.featured-companies .company__item {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  margin-top: 14px;
}
.featured-companies .company__item .item__img {
    width: 80px;
}
.featured-companies .company__item .item__details {
    padding-left: 7px;
    font-family: ProximaReg;
    margin-bottom: 5px;
    padding-left: 10px;
}
.featured-companies .company__item .item__details h5 {
      margin: 0;
      color: #59606b;
      font-size: 16px;
      line-height: 15px;
      font-weight: bold;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
}
.featured-companies .company__item .item__details p {
      color: #7c8695;
      margin: 0;
      font-size: 14px;
      line-height: 19px;
      margin-top: 2px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
}
.featured-companies .company__item .item__details a {
      font-size: 12px;
      color: black;
      line-height: 16px;
      display: inline-block;
}

.f-container {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.people-suggest {
  padding-top: 21px;
}
.people-suggest > h5 {
    color: #7c8695;
    font-family: ProximaReg;
    font-size: 14px;
    display: inline;
    width: 65%;
    margin-bottom: 28px;
}
.people-suggest > a {
    color: #59606b;
    font-family: ProximaReg;
    font-size: 14px;
    display: inline-block;
    width: 33%;
    text-align: right;
    margin-bottom: 10px;
}
.people-suggest .suggest__item {
    position: relative;
    margin-bottom: 18px;
}
.people-suggest .suggest__item h5 {
      width: 100%;
      font-family: ProximaReg;
      font-size: 16px;
      color: #59606b;
      margin-bottom: 4px;
}
.people-suggest .suggest__item .suggest__photo {
      width: 97px;
}
.people-suggest .suggest__item .suggest__photo img {
        width: 100%;
}
.people-suggest .suggest__item .suggest__details {
      height: 100%;
      align-self: stretch;
      padding-left: 8px;
      font-family: ProximaReg;
}
.people-suggest .suggest__item .suggest__details p {
        color: #7c8695;
        font-size: 14px;
        margin: 0;
        line-height: 18px;
        margin-bottom: -1px;
}
.people-suggest .suggest__item .suggest__details .details__loc {
        margin-top: 3px;
}
.people-suggest .suggest__item .suggest__details a {
        color: #59606b;
        font-size: 12px;
        display: inline-block;
        margin-top: 1px;
        font-weight: bold;
}
.featured-prod > h5 {
  color: #7c8695;
  font-family: ProximaReg;
  font-size: 14px;
  display: inline;
  width: 65%;
  margin-bottom: 21px;
}
.featured-prod > a {
  color: #59606b;
  font-family: ProximaReg;
  font-size: 14px;
  display: inline-block;
  width: 33%;
  text-align: right;
  margin-bottom: 14px;
}
.featured-prod .product__item {
  margin-bottom: 20px;
}
.featured-prod .product__item h5 {
    width: 100%;
    font-family: ProximaReg;
    font-size: 16px;
    color: #59606b;
    margin-bottom: 5px;
    line-height: 15px;
    font-weight: bold;
}
.featured-prod .product__item .product__photo {
    min-width: 80px;
    width: 80px;
}
.featured-prod .product__item .product__photo img {
      display: block;
      width: 100%;
}
.featured-prod .product__item .product__details {
    height: 100%;
    padding-left: 7px;
    font-family: ProximaReg;
}
.featured-prod .product__item .product__details p {
      color: #7c8695;
      font-size: 14px;
      margin: 0;
      width: 90%;
      line-height: 16px;
}
.adv-right h4 {
  font-family: ProximaReg;
  color: #7c8695;
  font-size: 14px;
  margin-top: 21px;
  margin-bottom: 15px;
}
.adv-right img {
  display: block;
  width: 100%;
}
.adv-right p {
  font-size: 11px;
  font-family: ProximaReg;
  color: #7c8695;
  margin: 7px 0;
}

.v--modal {
  padding: 28px;
  text-align: center;
}
.v--modal h3 {
    font-size: 5rem;
    font-family: ProximaLight;
    color: #87b448;
    margin-top: 0;
    margin-bottom: 35px;
}
.v--modal p {
    color: #aaa;
    font-family: ProximaReg;
    font-size: 1.2rem;
    margin: 0;
}
.v--modal-overlay {
  background: rgba(0, 0, 0, 0.5);
}
.service .service__head {
  padding-top: 9px;
  width: 100%;
}
.service .service__head h2 {
    font-family: ProximaReg;
    font-size: 30px;
    margin: 0;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.service .service__head button {
    white-space: nowrap;
    height: 30px;
    border: none;
    background-color: #87b448;
    border-radius: 5px;
    color: white;
    font-size: 14px;
    font-family: ProximaReg;
    display: block;
    margin-left: auto;
    margin-right: 0;
    cursor: pointer;
    padding-left: 21px;
    padding-right: 21px;
}
.service__list {
  margin-top: 10px;
}
.service__list .service__item {
    margin-bottom: 35px;
}
.service__list .service__item img {
      border: 1px solid #e0e2e6;
      display: block;
      width: 100%;
      padding: 21px;
      margin: 0 auto 18px;
      transition: background ease-in 0.2s;
      cursor: pointer;
}
.service__list .service__item h5 {
      font-family: ProximaReg;
      font-size: 14px;
      color: #59606b;
      font-weight: bold;
      text-align: center;
      transition: color ease 0.2s;
}
.service__list .service__item:hover img {
      background-color: #f4f7fb;
}
.service__list .service__item:hover h5 {
      color: #87b448;
}

.contacts__head {
  position: relative;
  z-index: 2;
  height: 65vh;
  width: 100%;
  position: relative;
  font-family: ProximaLight;
  background-image: url(/static/img/contacts_header.28fd5fc.png);
  background-position: center;
  background-size: cover;
}
.contacts__head .head__logo {
    display: block;
    margin-left: 50px;
    margin-top: 40px;
    margin-right: auto;
}
.contacts__head .head__btn {
    display: block;
    margin-left: auto;
    margin-right: 50px;
    margin-top: 40px;
    background-color: #87b448;
    border-radius: 4px;
    border: none;
    color: white;
    padding: 7px 14px;
}
.contacts__head .head__text {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: white;
}
.contacts__head .head__text h2 {
      font-size: 48px;
      margin-bottom: 21px;
}
.contacts__head .head__text p {
      width: 25%;
      margin: 0 auto;
      font-size: 20px;
}
.form-area {
  background-image: url(/static/img/bg.9588b5d.png);
  background-position: center;
  background-size: cover;
  padding-bottom: 100px;
}
.form-area .form-wrapp {
    position: relative;
    z-index: 5;
    display: block;
    max-width: 970px;
    width: 100%;
    margin: 0 auto;
    background-color: white;
    border-radius: 5px;
    margin-top: -100px;
    padding: 20px;
    border: 1px solid #dbdbdb;
}
.form-area .form-wrapp .detail_input {
      display: none;
}
.form-area .form-wrapp select {
      display: block;
      width: 100%;
      margin-bottom: 28px;
      border: none;
      border-radius: 4px;
      padding: 7px 14px;
      height: 50px;
      line-height: 50;
      border: 1px solid #dbdbdb;
}
.form-area .form-wrapp label {
      display: block;
      font-family: ProximaReg;
      font-size: 16px;
      color: #59606b;
}
.form-area .form-wrapp input {
      height: 50px;
      line-height: 50px;
      display: block;
      width: 100%;
      border-radius: 5px;
      border: 1px solid #dbdbdb;
      padding: 7px 14px;
      margin-bottom: 21px;
}
.form-area .form-wrapp .textarea-labels label {
      display: inline-block;
      width: 49%;
}
.form-area .form-wrapp .textarea-labels label:last-child {
        text-align: right;
}
.form-area .form-wrapp textarea {
      border-radius: 3px;
      background-color: white;
      display: block;
      width: 100%;
      border-radius: 3px;
      border: 1px solid #dbdbdb;
      padding: 7px 14px;
      margin-bottom: 21px;
}
.form-area .form-wrapp .drop-photo-area {
      display: inline-block;
      background-color: #f7f7f7;
      width: 200px;
      height: 200px;
      padding: 21px;
      text-align: center;
      border-radius: 5px;
      border: 1px solid #dbdbdb;
      font-family: ProximaReg;
      padding-top: 40px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
}
.form-area .form-wrapp #upload-preview {
      object-fit: contain;
      object-position: center;
      height: 200px;
      width: 200px;
}
.form-area .sbmt-btn {
    display: block;
    height: 50px;
    line-height: 50px;
    background-color: #87b448;
    color: white;
    border: none;
    border-radius: 5px;
    width: 970px;
    margin-top: 21px;
    margin-left: auto;
    margin-right: auto;
    font-family: ProximaReg;
    font-size: 16px;
}
.loaded-img {
  height: 200px;
  width: 200px;
  object-fit: contain;
  object-position: center;
  border-radius: 5px;
}
.img-input {
  position: absolute;
  display: block;
  width: 200px;
  height: 200px !important;
  top: 0;
  left: 0;
  padding: 0 !important;
  opacity: 0;
  cursor: pointer;
}
.contacts__about {
  padding: 100px 50px;
}
.contacts__about h2 {
    font-family: ProximaLight;
    text-align: center;
    margin-bottom: 50px;
}
.contacts__about p {
    font-family: ProximaLight;
    width: 80%;
    margin: 0 auto;
    font-size: 16px;
}
.contacts__footer {
  padding: 30px 0;
  font-family: ProximaReg;
  font-size: 12px;
  border-top: 1px solid #dbdbdb;
  color: #b7bfcd;
}
.contacts__footer h5 {
    font-family: ProximaReg;
    font-size: 12px;
    margin: 0 0 0 28px;
}
.contacts__footer ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: block;
    text-align: right;
}
.contacts__footer ul li {
      display: inline-block;
      margin-right: 28px;
}
.contacts__footer ul li a {
        color: #b7bfcd;
}
